.nav {
  background-color: $whitesmoke;

  &__menu-search {
    display: flex;
  }

  &__menu {
    background-color: $white;
    color: $black;
    display: block;
  }

  &__menu-reverse {
    background-color: $black;
    color: $white;
    border: 1px solid $white;
  }

  &__bar {
    display: flex;
    max-width: 1440px;
    justify-content: space-between;
    align-items: center;
    flex: 1 0 0;
    margin: auto;
    width: 100%;
    min-height: 78px;
    padding: 0 128px;
  }

  &__links {
    display: flex;
    align-items: flex-start;
    gap: 48px;
    align-self: stretch;
    place-items: center;

    @include bp(tablet) {
      display: none;
    }
  }

  &__dropdown {
    display: none;
    position: absolute;
    background-color: $monochromeBlack;
    text-align: center;
    padding: 40px 42px 0;
    height: 100vh;
    width: 100%;
    grid-auto-rows: minmax(min-content, 0);

    .nav__links__list {
      display: flex;
      justify-content: space-between;
      flex-direction: column;
      gap: 30px;
    }
  }

  &__items {
    color: $white;
    font-family: 'Inter', sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    text-transform: uppercase;
    position: relative;

    @include bp(tablet) {
      font-size: 20px;
      font-weight: 500;
      line-height: 140%; /* 28px */
      letter-spacing: -0.6px;
      color: $white;
    }
  }

  &__logo {
    max-width: 50px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    padding: 5px 0;
  }

  &__logo__black {
    display: block;
  }

  &__logo__white {
    display: none;
  }
}

.brand__container {
  position: absolute;
  left: 5rem;
  top: 10px;
  overflow: hidden;
  z-index: 2;
  display: flex;
  transition: all 0.2s ease-out;

  @include bp(tablet) {
    left: 0;
    right: 0;
    margin: auto;
    width: 200px;
  }
}

.fixed-position {
  position: fixed;
}

.no-scroll {
  overflow-y: hidden;
}
